@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;700;900&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}html,body{min-height:100vh}html{font-family:"Maven Pro", sans-serif}body{padding:0 1rem}ul{list-style:none}a{text-decoration:none}


.app-layout{display:grid;grid-template-rows:[header-start] minmax(20vh, auto) [header-end main-start] 1fr [main-end footer-start] 20vh [footer-end];grid-row-gap:1rem;row-gap:1rem;min-height:100vh}

.header-bottom{padding:1rem 0}.header-bottom .nav-menu{display:flex;justify-content:space-around;font-size:1rem;text-transform:uppercase}.header-bottom .nav-link{font-weight:700;color:#242323}

.header-top{display:grid;grid-auto-flow:column;align-items:center;padding-top:1.5rem}.header-top .header-logo{justify-self:center}.header-top .cart-icon{justify-self:end}.header-top .input-search{justify-self:start}

.cart-modal{border:1px solid gray;box-shadow:0 0 10px 4px rgba(0,0,0,0.356);width:45vw;display:grid;grid-template-rows:repeat(3, -webkit-min-content);grid-template-rows:repeat(3, min-content);grid-row-gap:1rem;row-gap:1rem;max-height:30vmax;overflow-y:scroll}.cart-modal .cart-modal-subtotal{display:flex;padding:0.7rem 1.2rem;justify-content:end;text-transform:uppercase}.cart-modal .cart-modal-button{max-width:100%;padding:1.2rem 1rem;text-align:center;cursor:pointer}.cart-modal .cart-modal-button:hover{border:none;border-top:1px solid gray;border-bottom:1px solid gray}.cart-modal .cart-modal-empty{display:flex;justify-content:center;padding:1.5rem 0.7rem}.cart-modal .cart-modal-button-disabled{background-color:#918d8d}.cart-modal .cart-modal-button-disabled:hover{cursor:not-allowed;background-color:#be9191;border:none}

.custom-button{padding:0.5rem 1.7rem;background-color:rgba(34,34,34,0.882353);transition:all 400ms ease;max-width:-webkit-max-content;max-width:-moz-max-content;max-width:max-content;color:#e6e6e6;text-transform:uppercase;letter-spacing:1.5px}.custom-button:hover{border:1px solid black;background-color:transparent;color:black}

.cart-item{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, -webkit-min-content);grid-template-rows:repeat(2, min-content);grid-row-gap:0.5rem;row-gap:0.5rem;padding:1rem 0.7rem}.cart-item:not(:last-of-type){border-bottom:1px solid #cecece}.cart-item .cart-item-name{grid-column:2 / span 2;justify-self:center;text-align:center;word-wrap:break-word;overflow-wrap:break-word;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}.cart-item .cart-item-remove-btn{justify-self:center;cursor:pointer}.cart-item .cart-item-qty{grid-column:2 / span 1;grid-row:2;justify-self:start}.cart-item .cart-item-price{grid-column:3 / span 1;grid-row:2;justify-self:end}

.image-container{display:inline-block;max-width:100vw}.image-container img{width:100%}

.cart-icon{display:grid;justify-items:center;position:relative;margin-right:1rem;position:relative}.cart-icon:hover .cart-modal{visibility:visible;opacity:1}.cart-icon .cart-modal{z-index:1;background-color:white;position:absolute;top:140%;right:0%;visibility:hidden;opacity:0;transition:all 100ms ease}.cart-icon .cart-total-items{position:absolute;top:-45%;left:90%;padding:0.2rem 0.45rem;border-radius:50%;background-color:#2862cf;color:white;text-align:center;font-weight:700}.cart-icon svg{width:2rem;height:2rem}

.input-search{display:grid;grid-auto-flow:column;grid-column-gap:0.4rem;-webkit-column-gap:0.4rem;column-gap:0.4rem}.input-search input[type="search"]{border:none;border-bottom:1.5px solid #bbbaba}.input-search input[type="search"]:focus{outline:none}.input-search input[type="search"]::-webkit-input-placeholder{letter-spacing:1.5px}.input-search input[type="search"]:-ms-input-placeholder{letter-spacing:1.5px}.input-search input[type="search"]::placeholder{letter-spacing:1.5px}

.search-button{width:2.2rem;height:2.2rem;background-color:#4173ce;border:none}.search-button svg{width:1.2rem;height:1.2rem;transform:translateY(8%);fill:white}

.header{display:grid;grid-row-gap:1rem;row-gap:1rem;margin-bottom:1rem;padding:0 1rem}


.loading{display:flex;justify-content:center;align-items:center}.spinner{background-color:#009688;height:100px;width:100px;border-radius:50%;position:relative;background:conic-gradient(#009688, transparent);animation:loadingSpinnerAnimation 1s linear infinite}.spinner::after,.spinner::before{content:"";position:absolute;border-radius:50%}.spinner::before{width:80px;height:80px;top:10px;left:10px;background-color:#ffffff}.spinner::after{height:10px;width:10px;background-color:#009688;top:0;left:45px}@keyframes loadingSpinnerAnimation{from{transform:rotateZ(0)}to{transform:rotateZ(360deg)}}

.error-container{display:grid;justify-self:center;justify-items:center;width:40%;grid-row-gap:2rem;row-gap:2rem;text-align:center}.error-container .error-heading{letter-spacing:1.5px;text-transform:uppercase;font-size:1.4rem}.error-container .error-message{letter-spacing:1.5px;text-transform:uppercase;font-size:1.2rem}.error-container img{width:100%}

